import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IEntrega } from '../models/entrega';
import { EntregasService } from '../service/entregas.service';
import { Subject, Subscription, debounceTime, distinctUntilChanged, firstValueFrom, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { SaveComponent } from './save/save.component';
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { PwaService } from '@shared/service/app/pwa.service';
@Component({
    selector: 'app-entregas',
    templateUrl: './entregas.component.html',
    styleUrls: ['./entregas.component.scss']
})
export class EntregasComponent implements OnInit, OnDestroy {
    entregas: IEntrega[];
    public dataSource: MatTableDataSource<IEntrega> = new MatTableDataSource();
    headers = ['Fecha Precarga', 'Fecha Entrega', 'Responsable', 'Observaciones', 'Deposito', 'Usuario Encargado', 'Estado', 'Acciones'];
    column_params = ['fecha_hora_precarga', 'fecha_hora_entrega', 'nick_usuario', 'observaciones', 'descripcion_deposito', 'nick_usuario_entrega', 'descripcion_estado', 'acciones'];
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['nick_usuario', 'descripcion_deposito', 'nick_usuario_entrega'];
    filter: IFilter = {
        deposito: -1,
        filtroFechaInicio: '',
        filtroFechaFin: ''
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.stock.view_stock_entregas + '?sort=-fecha_hora';
    search: FormControl = new FormControl('');
    sortValue: string;
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();


    constructor(
        private _entregasService: EntregasService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private _menuService: JedsMenuService,
        private router: Router,
        public _headerTextService: HeaderTextService,
        private pwaService: PwaService

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [
            {
                nombre_boton: "Editar",
                functionName: 'editar',
                iconoAccion: (entrega: IEntrega) => 'jedstion:editar',
                iconoAccionado: (entrega: IEntrega) => '',
                iconoDeshabilitado: (entrega: IEntrega) => '',
            },
            {
                nombre_boton: "Eliminar",
                functionName: 'eliminar',
                iconoAccion: (entrega: IEntrega) => 'jedstion:eliminar',
                iconoAccionado: (entrega: IEntrega) => '',
                iconoDeshabilitado: (entrega: IEntrega) => '',
            },
            {
                nombre_boton: "Reporte",
                functionName: 'reporte',
                iconoAccion: (entrega: IEntrega) => 'jedstion:imprimir',
                iconoAccionado: (entrega: IEntrega) => '',
                iconoDeshabilitado: (entrega: IEntrega) => '',
            }
        ];
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
                data: this.tabla.getElementosExistentes()
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.deposito != -1 ? "filter[iddeposito]=" + this.filter.deposito + '&' : '';
                        this.extraParams += this.filter.usuario_responsable != '' ? "filter[nick_usuario]=" + this.filter.usuario_responsable + '&' : '';
                        this.extraParams += this.filter.usuario_encargado != '' ? "filter[nick_usuario_entrega]=" + this.filter.usuario_encargado + '&' : '';
                        this.extraParams += this.filter.filtroFechaInicio != null ? "filter[fecha][gte]=" + this.filter.filtroFechaInicio + '&' : '';
                        this.extraParams += this.filter.filtroFechaFin != null ? "filter[fecha][lte]=" + this.filter.filtroFechaFin + '&' : '';
                        this.tabla.filters(this.filtroBusqueda);
                        // this._searchService.getSearchInput().setValue(this._tabsServices.getFiltroBuscadorPedido());
                    }
                },
            });
    }

    /**
 * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
 * @param event 
 */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                this.updateEntrega(event);
                break;
            case 'eliminar':
                this.deleteEntrega(event);
                break;
            case 'reporte':
                this.reporteEntrega(event);
                break;
        }
    }

    /**
     * Esta funcion guarda un item
     */
    saveEntrega() {
        this.dialogo
            .open(SaveComponent, { panelClass: 'ayuda-dialog' })
            .componentInstance.creadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Entrega creada con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion modifica un item
     * @param event 
     */
    updateEntrega(event) {
        this.dialogo
            .open(SaveComponent, { data: event.params.data, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => {
                    this.alert.success("Entrega editada con éxito.");
                    this.tabla.filters(this.filtroBusqueda);
                },
            });
    }

    /**
     * Esta funcion elimina un item
     * @param event 
     */
    deleteEntrega(event) {
        this.confirm.open({
            title: "Eliminar Entrega: " + event.params.data.fecha,
            message: `¿Desea eliminar la entrega realizada el: ${event.params.data.fecha} ${event.params.data.hora}; del depósito: ${event.params.data.descripcion_deposito} ?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._entregasService.deleteEntrega(event.params.data.identrega).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Entrega eliminada con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Esta entrega no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    /**
     * Esta funcion Genera un Reporte y lo muestra en una nueva pestaña
     */
    reporteEntrega(event) {
        const idEntrega = event.params.data.identrega;
        this._entregasService.reporteEntrega(idEntrega).subscribe(
            (data) => {
                if (data.success) {
                    const reportePath = data.path;
                    if (this.pwaService.isPwa()) {
                        this.router.navigate(['/reporte', reportePath]);
                    } else {
                        const reporteUrl = `${environment.baseRest}/${reportePath}`;
                        window.open(reporteUrl, '_blank');
                    }
                } else {
                    console.error('Error al generar el reporte');
                }
            },
            (error) => {
                console.error('Error en la solicitud del reporte', error);
            }
        );
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe();
        this.dialogo.closeAll();
    }
}